@tailwind base;
@tailwind components;
@tailwind utilities;

body{
    background-color: #f7f7f7;
}

.menu-btn{
    @apply w-10 h-10 grid place-items-center bg-zinc-50/100 hover:bg-zinc-50/90 rounded-xl
}
.navbar.active{
    @apply opacity-100 scale-100 blur-0 visible
}
.nav-link{
    @apply grid items-center px-4 py-3 text-sm font-medium text-gray-600 hover:text-gray-500 transition-colors
}
.nav-link.active{
    @apply text-[#7eb895] duration-300
}


.lable{
    @apply block text-sm text-gray-600 font-normal mb-2
}
.text-field{
    @apply block outline-none w-full px-3 py-2 text-sm bg-gray-200/50 rounded-xl ring-1 ring-inset ring-transparent 
    transition-shadow focus:ring-[#7eb895] focus:ring-2
}
